import React, { useEffect, useState } from 'react';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import { UserRoleAPI } from '../services/UserRoleAPI';

const UserContext = React.createContext();

export const UserProvider = ({ children }) => {
  const [role, setRole] = useState({
    IsBuyer: false,
    IsSeller: false,
  });

  const fetchRole = async () => {
    const { data } = await UserRoleAPI.fetchUserRole();
    if (_isEmpty(data)) {
      setRole({});
      return;
    }

    const isSeller = _get(data, 'IsSeller');
    setRole({ IsSeller: isSeller, IsBuyer: !isSeller, values: data });
  };

  const switchToBuyerMode = () => setRole({
    ...role, IsBuyer: true,
    IsSeller: false,
  });

  const switchToSellerMode = () => setRole({
    ...role, IsBuyer: false,
    IsSeller: true,
  });

  useEffect(() => {
    fetchRole();
  }, []);

  return (
    <UserContext.Provider value={{ ...role, fetchRole, switchToBuyerMode, switchToSellerMode }}>
      {children}
    </UserContext.Provider>
  );
};

export default UserContext;
